<template>
  <v-content>
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>

    <div v-if="overlay === false">
      <v-row align="center">
        <v-col style="padding:12px 0px 5px 20px" cols="auto">
          <v-avatar size="80">
            <img :src="user.avatar" />
          </v-avatar>
        </v-col>
        <v-col style="padding:12px 3px 3px 1%" cols="5">
          <v-row align="center" justify="center" no-gutters>
            <h1>{{$t("titleTrainer")}}!</h1>

            <v-col style="padding:5px 0 0 5%">
              <settings></settings>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row align="start" justify="center" no-gutters>
        <v-col cols="2" no-gutters style="padding:12px 0 12px 12px; !important max-height: 80vh">
          <v-sheet style="height:86vh;padding-right:12px" class="overflow-y-auto">
            <v-list style="width:100%;padding-top:0">
              <v-list-item v-for="(client, index) in clients" :key="index">
                <v-list-item-content class="client" @click="clientCalendar(client)">
                  <v-list-item-title>{{$t('clientInformation.title')}} {{index + 1}}</v-list-item-title>
                </v-list-item-content>
                <seeHowYouFeel :user="client.user"></seeHowYouFeel>
                <v-btn
                  @click="coordinates($event);setIndexAndOpenMenu(index); "
                  :large="true"
                  :text="true"
                  icon
                  style="margin-left:2%"
                >
                  <v-icon large color="black">visibility</v-icon>
                </v-btn>

                <v-menu
                  :nudge-width="300"
                  :position-x="x"
                  :position-y="y"
                  v-model="clientData"
                  v-if="indexClient == index"
                  :close-on-content-click="false"
                >
                  <!-- <template v-slot:activator="{ on }">
                    <v-btn v-on="on" :large="true" :text="true" icon>
                      <v-icon large color="black">visibility</v-icon>
                    </v-btn>
                  </template>-->
                  <v-card height="325">
                    <v-row justify="center" style="margin:0">
                      <h2>{{$t('clientInformation.title')}} {{index + 1}}</h2>
                    </v-row>
                    <v-divider></v-divider>
                    <v-row style="margin:0" align="center" justify="center">
                      <v-col class="center">{{$t('clientInformation.program')}}</v-col>
                      <v-divider vertical></v-divider>
                      <v-col>
                        <v-text-field
                          v-model="values.program.find(el => el.id == client.program).name[$i18n.locale]"
                          :full-width="true"
                          filled
                          dense
                          readonly
                          rounded
                          class="fieldStyle"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-divider></v-divider>
                    <v-row style="margin:0" align="center" justify="center">
                      <v-col class="center">{{$t('clientInformation.lastWeight')}}</v-col>
                      <v-divider vertical></v-divider>
                      <v-col>
                        <v-text-field
                          v-model="client.lastWeightNumber"
                          :full-width="true"
                          filled
                          dense
                          readonly
                          rounded
                          :suffix="values.systems[user.systemType].weight.symbol"
                          class="fieldStyle"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-divider></v-divider>
                    <v-row style="margin:0" align="center" justify="center">
                      <v-col class="center">{{$t('clientInformation.height')}}</v-col>
                      <v-divider vertical></v-divider>
                      <v-col>
                        <v-text-field
                          v-model="client.heightNumber"
                          :full-width="true"
                          filled
                          dense
                          readonly
                          rounded
                          :suffix="values.systems[user.systemType].height.symbol"
                          class="fieldStyle"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-divider></v-divider>
                    <v-row style="margin:0" align="center" justify="center">
                      <v-col class="center">{{$t('clientInformation.target')}}</v-col>
                      <v-divider vertical></v-divider>
                      <v-col>
                        <v-text-field
                          v-model="values.target.find(el => el.id == client.target).name[$i18n.locale]"
                          :full-width="true"
                          filled
                          dense
                          readonly
                          rounded
                          class="fieldStyle"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-divider></v-divider>
                  </v-card>
                </v-menu>
              </v-list-item>
            </v-list>
          </v-sheet>
        </v-col>

        <v-divider vertical></v-divider>
        <v-col>
          <calendar></calendar>
        </v-col>

        <v-col cols="4">
          <charts></charts>
        </v-col>
      </v-row>
    </div>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";
import calendar from "../components/calendar";
import charts from "../components/charts";
import settings from "../components/settingsTrainer";
import seeHowYouFeel from "../components/seeHowYouFeel";

export default {
  name: "Trainer",
  components: { calendar, charts, settings, seeHowYouFeel },
  data: () => ({
    openTrainerSignUp: false,
    openClientSignUp: false,
    overlay: false,
    x: 300,
    y: 200,
    clientData: false,
    indexClient: 0,
    clientsArray: []
  }),
  computed: mapGetters(["user", "clients", "types", "values"]),
  async created() {
    this.overlay = true;
    await this.$store.dispatch("getUser");
    await this.$store.dispatch("types");
    await this.$store.dispatch("values");
    await this.$store.dispatch("getClientsTrainer");
    await this.$store.commit("activeClient", this.clients[0]);
    await this.$store.dispatch("getRoleInfo");

    this.overlay = false;
  },
  methods: {
    coordinates(e) {
      this.y = e.clientY;
    },
    setIndexAndOpenMenu(index) {
      this.indexClient = index;
      this.clientData = true;
    },
    clientCalendar: function(data) {
      this.$store.commit("activeClient", data);
    }
  }
};
</script>

<style>
.overflowText {
  display: inline-block;
  width: 120px;
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: ellipsis;
}

.client:hover {
  cursor: pointer;
  background: #f5f5f5;
  transition: 0.5s cubic-bezier(0.25, 0.8, 0.5, 1);
}

.center {
  text-align: center;
}
.fieldStyle {
  height: 44px;
  margin-top: 5px;
}
</style>