<template>
  <div>
    <v-btn @click="openRate" small :text="true" icon>
      <v-icon large color="black">grade</v-icon>
    </v-btn>
    <v-dialog v-model="rate" width="800" persistent>
      <v-sheet>
        <v-row justify="center" style="margin:0">
          <h2>How the client feels for this month: {{$t(`months[${calendarMonth - 1}]`)}} {{calendarYear}}</h2>
        </v-row>
        <v-divider></v-divider>
        <div v-for="(week, index) in weeksRanges" :key="index">
          <v-row justify="center" style="margin:0">
            <h2>Week {{index + 1}}</h2>
          </v-row>
          <v-divider></v-divider>
          <v-row justify="center" style="margin:0">
            <v-rating
              v-if="userRates[index]"
              v-model="userRates[index].rate"
              color="yellow darken-3"
              background-color="grey darken-1"
              empty-icon="$ratingFull"
              half-increments
              hover
              readonly
            ></v-rating>
            <h3 v-else>No Rating</h3>
          </v-row>
          <v-divider></v-divider>
        </div>
        <v-row style="padding:20px 3% 20px 3%" align="center" justify="space-between" no-gutters>
          <v-btn @click="close" color="error" :normal="true">Close</v-btn>
        </v-row>
      </v-sheet>
    </v-dialog>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Rate",
  props: ["user"],
  data() {
    return {
      rate: false
    };
  },
  computed: mapGetters([
    "calendarMonth",
    "weeksRanges",
    "calendarYear",
    "userRates"
  ]),
  methods: {
    async openRate() {
      this.$emit("load");
      const data = {
        user: this.user,
        month: this.calendarMonth,
        year: this.calendarYear
      };
      await this.$store.dispatch("getRatesUser", data);
      this.$emit("stopLoad");
      this.rate = true;
    },
    close() {
      this.rate = false;
    }
  }
};
</script>
